import React, { useState } from 'react';
import TrackVisibilty from 'react-on-screen';

const TrackComponent = ({ isVisible }) => {
    const [investors, setInvestors] = useState(0);
    const [assets, setAssets] = useState(0);
    const [experience, setExperience] = useState(0);

    if (!isVisible) {
        return (
            <section className="whybajaj whynumbercontainer pt40 pb40">
                <div className="container">
                    <div className="row" id="whynumbers">
                        <div className="col-md-4 text-center whynumber" >
                            Investors <h5>27 Lakhs +</h5>
                        </div>
                        <div className="col-md-4 text-center whynumber">
                            Assets  <h5> ₹ 22,700 Cr +</h5>
                        </div>
                        <div className="col-md-4 text-center whynumber">
                            Experience <h5> 54+ Years</h5>
                        </div>
                    </div>
                </div>
            </section>
        )
    }

    const countUp = () => {
        if (investors < 27) {
            setTimeout(() => {
                setInvestors(investors + 1);
            }, 50)
        }
        if (assets < 22) {
            setTimeout(() => {
                setAssets(assets + 1);
            }, 50)
        }
        if (experience < 54) {
            setTimeout(() => {
                setExperience(experience + 1);
            }, 25)
        }
    }
    if (isVisible) {
        countUp();
    }
    return (
        <section className="whybajaj whynumbercontainer pt40 pb40">
            <div className="container">
                <div className="row" id="whynumbers">
                    <div className="col-md-4 text-center whynumber" >
                        Investors <h5>{investors} Lakhs +</h5>
                    </div>
                    <div className="col-md-4 text-center whynumber">
                        Assets  <h5> ₹ {assets},700 Cr +</h5>
                    </div>
                    <div className="col-md-4 text-center whynumber">
                        Experience <h5> {experience}+ Years</h5>
                    </div>
                </div>
            </div>
        </section>
    )

}


export default () => {
    return (
        <TrackVisibilty>
            <TrackComponent />
        </TrackVisibilty>
    )
}
